<template>
  <div class="m-order-table">
    <div class="search" style="margin: 15px 0;width: 400px;display:inline-block;vertical-align:middle;">
      <el-input placeholder="请输入搜索内容" v-model="searchKeyword" class="input-with-select">
        <el-select v-model="searchSelect" slot="prepend" placeholder="请选择">
          <el-option label="团队管理者" value="group_leader"></el-option>
          <el-option label="业务经理" value="user_name"></el-option>
          <el-option label="业务客户" value="customer_name"></el-option>
          <el-option label="合同号" value="contract_number"></el-option>
          <el-option label="车牌号" value="car_license"></el-option>
        </el-select>
        <el-button @click="search" slot="append" icon="el-icon-search"></el-button>
      </el-input>
    </div>
    <div style="display: inline-block;">
      <el-select v-if="noAction" style="width: 150px;" v-model="filter.bill_status" clearable placeholder="请选择订单状态" @change="handleFilterStatusChange">
        <el-option
          v-for="item in statusDict"
          :key="item.value"
          :label="item.name"
          :value="item.value">
        </el-option>
      </el-select>
      <el-select style="width: 150px;" v-model="filter.company_id" clearable placeholder="请选择公司" @change="handleFilterCompanyChange">
        <el-option
          v-for="item in companyDict"
          :key="item.company_id"
          :label="item.company_name"
          :value="item.company_id.toString()">
        </el-option>
      </el-select>
      <el-date-picker
        @change="handleFilterDateRangeChange"
        v-model="filter.dateRange"
        value-format="yyyy-MM-dd"
        type="daterange"
        range-separator="至"
        start-placeholder="借款开始时间"
        end-placeholder="借款结束时间">
      </el-date-picker>
    </div>
    <el-table
      v-loading="loading"
      :data="rows_inShow"
      stripe
      style="width: 100%">
      <el-table-column
        prop="bill_id"
        label="订单号">
      </el-table-column>
      <el-table-column
        label="客户名称"
        :width="100">
        <template slot-scope="scope">{{ scope.row.customer_name }}</template>
      </el-table-column>
      <el-table-column
        v-for="col in cols"
        :key="col.prop"
        :prop="col.prop"
        :label="col.label"
        :width="col.width">
      </el-table-column>
      <el-table-column
        v-if="!noAction"
        fixed="right"
        label="操作"
        :width="stage === '3'?260:160">
        <template slot-scope="scope">
          <div>
            <el-button @click="detail(scope.row)" type="text" size="small">订单详情</el-button>
            <el-button v-if="stage !== '3'" @click="audit(scope.row)" type="text" size="small">审核</el-button>
            <el-button v-if="stage === '3'" @click="auditP(scope.row)" type="text" size="small">审核(个人)</el-button>
            <el-button v-if="stage === '3'" @click="auditC(scope.row)" type="text" size="small">审核(公司)</el-button>
            <el-button @click="reject(scope.row)" type="text" size="small">驳回</el-button>
          </div>
        </template>
      </el-table-column>
      <el-table-column
        v-else
        fixed="right"
        label="操作"
        width="120">
        <template slot-scope="scope">
          <div>
            <el-button @click="detail(scope.row)" type="text" size="small">订单详情</el-button>
            <el-button @click="deleteOrder(scope.row)" type="text" size="small">删除</el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      style="margin: 20px 0;text-align:center;"
      background
      :page-size="page_size"
      :total="total"
      layout="prev, pager, next"
      :current-page="page"
      @current-change="handlePageChange">
    </el-pagination>
  </div>
</template>
<script>
import { rest } from '@/common/request';
import { percentDisplay, currencyDisplay } from '@/common/utils';

export default {
  props:['noAction','stage'],
  data(){
    return{
      cols:[
        {
          prop: 'company',
          label: '所属分公司',
          width: 150
        },
        {
          prop: 'car_lincese',
          label: '车牌号',
          width: 100
        },
        {
          prop: 'product',
          label: '借款产品',
          width: 120
        },
        {
          prop: 'load_type',
          label: '借款类型'
        },
        {
          prop: 'month_rate',
          label: '月利率'
        },
        {
          prop: 'bill_status',
          label: '审批状态',
          width: 120
        },
        {
          prop: 'loan_amount',
          label: '放款金额',
          width: 120
        },
        {
          prop: 'contract_amount',
          label: '合同金额',
          width: 120
        },
        {
          prop: 'loan_period',
          label: '借款期限'
        },
        {
          prop: 'loan_start',
          label: '借款起始日',
          width: 100
        },
        {
          prop: 'loan_end',
          label: '借款终止日',
          width: 100
        },
        {
          prop: 'creadit',
          label: '是否信用贷',
          width: 100
        },
        {
          prop: 'group',
          label: '团队',
          width: 100
        },
        {
          prop: 'username',
          label: '客户经理 ',
          width: 100
        },
        {
          prop: 'group_leader',
          label: '团队经理 ',
          width: 100
        }
      ],
      rows: [],
      loading: true,
      page: 1,
      page_size: 20,
      total: 0,
      searchSelect: null,
      searchKeyword: null,
      filter:{
        bill_status: '',
        company_id: '',
        dateRange: null
      },
      companyDict: [],
      statusDict:[
        {name: '审核驳回', value: '-1'},
        {name: '草稿', value: '0'},
        {name: '风控专员审核中', value: '1'},
        {name: '风控总监审核中', value: '2'},
        {name: '客服专员签约中', value: '3'},
        {name: '财务审批中', value: '4'},
        {name: '审核完成', value: '5'}
      ]
    }
  },
  computed:{
    'rows_inShow'(){
      let statusDict = {
        '-1':'审核驳回',
        '0':'草稿',
        '1':'风控专员审核中',
        '2':'风控总监审核中',
        '3':'客服专员签约中',
        '4':'财务审批中',
        '5':'审核完成'
      }
      let loadProductDict = {
        '1': '全款车押证',
        '2': '全款车押车'
      }
      let loadTypeDict = {
        '0': '-',
        '1': '先息后本',
        '2': '等额等息'
      }
      let rows_inShow = this.rows.map(item => {
        item.bill_status = statusDict[item.bill_status.toString()];
        item.loan_period = item.loan_period + '期';
        item.loan_amount = currencyDisplay(item.loan_amount);
        item.contract_amount = currencyDisplay(item.contract_amount);
        item.month_rate = percentDisplay(item.month_rate);
        item.creadit?item.creadit = '是':item.creadit = '否';
        item.product = loadProductDict[item.product];
        item.load_type = loadTypeDict[item.load_type];
        return item
      })
      return rows_inShow;
    }
  },
  methods:{
    fetchCompanyDict(){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/dict',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'GET'
      }).then(res => {
        this.companyDict = res.data.company;
      })
    },
    fetchList(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/bill',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).then(res => {
        this.rows = res.data.data;
        this.total = res.data.total;
        this.loading = false;
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    reloadList(){
      let data = {
        company_id: this.filter.company_id,
        bill_status: this.stage,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      if(this.searchSelect){data[this.searchSelect] = this.searchKeyword;}
      if(this.page === 1){
        this.fetchList(data);
      }else{
        this.page = 1;
      }
    },
    deleteItem(data){
      let uid = sessionStorage.getItem('ts_user_id');
      return rest({
        url: '/v1/inspect/'+ uid +'/bill/del',
        headers:{
          'X-Inspect-Token': sessionStorage.getItem('ts_userToken')
        },
        method: 'POST',
        data: data
      }).catch(res => {
        this.$message.error(res.response.data.msg);
      })
    },
    deleteOrder(row){
      let data = {
        company_id: this.filter.company_id,
        bill_status: this.stage,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      this.$confirm('本次操作将永久删除该订单, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteItem({bill_id: row.bill_id}).then(res => {
          this.fetchList(data);
        });
      }).catch(() => {});
    },
    detail(row){
      this.$emit('detail', row);
    },
    audit(row){
      this.$emit('audit', row);
    },
    auditP(row){
      this.$emit('auditPerson', row);
    },
    auditC(row){
      this.$emit('auditCompany', row);
    },
    reject(row){
      this.$emit('reject', row);
    },
    handlePageChange(val){
      this.page = val;
      let data = {
        company_id: this.filter.company_id,
        order: "asc",
        page: val,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      if(this.searchSelect){data[this.searchSelect] = this.searchKeyword;}
      this.fetchList(data);
    },
    search(){
      let data = {
        company_id: this.filter.company_id,
        bill_status: this.stage,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      if(this.searchSelect){data[this.searchSelect] = this.searchKeyword;}
      if(this.page === 1){
        this.fetchList(data);
      }else{
        this.page = 1;
      }
    },
    handleFilterStatusChange(val){
      let data = {
        company_id: this.filter.company_id,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?val:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      if(this.page === 1){
        this.fetchList(data);
      }else{
        this.page = 1;
      }
    },
    handleFilterCompanyChange(val){
      let data = {
        company_id: val,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
        loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
      }
      if(this.page === 1){
        this.fetchList(data);
      }else{
        this.page = 1;
      }
    },
    handleFilterDateRangeChange(val){
      let data = {
        company_id: this.filter.company_id,
        bill_status: this.stage,
        order: "asc",
        page: this.page,
        page_size: this.page_size,
        bill_status: this.noAction?this.filter.bill_status:this.stage,
        loan_start: val?val[0]:'',
        loan_end: val?val[1]:''
      }
      if(this.page === 1){
        this.fetchList(data);
      }else{
        this.page = 1;
      }
    }
  },
  mounted(){
    this.fetchCompanyDict();
    this.fetchList({
      company_id: this.filter.company_id,
      bill_status: this.stage,
      order: "asc",
      page: this.page,
      page_size: this.page_size,
      bill_status: this.noAction?this.filter.bill_status:this.stage,
      loan_start: this.filter.dateRange?this.filter.dateRange[0]:'',
      loan_end: this.filter.dateRange?this.filter.dateRange[1]:''
    });
  }
}
</script>
<style>
  .m-order-table .el-table td{
    padding: 5px 0;
  }
  .m-order-table .search .el-select .el-input {
    width: 130px;
  }
</style>





