            <el-col :span="12">不可撤销承诺书：<span v-if="item.sign.chennuoshu.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.chennuoshu.length !== 0">
            <el-col :span="6" v-for="img in item.sign.chennuoshu" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">单签公司合同：<span v-if="item.sign.danqian.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.danqian.length !== 0">
            <el-col :span="6" v-for="img in item.sign.danqian" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">抵押合同：<span v-if="item.sign.mortgage_contract.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.mortgage_contract.length !== 0">
            <el-col :span="6" v-for="img in item.sign.mortgage_contract" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">反担保补充协议：<span v-if="item.sign.fandanbaobuchong.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.fandanbaobuchong.length !== 0">
            <el-col :span="6" v-for="img in item.sign.fandanbaobuchong" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">反担保协议：<span v-if="item.sign.fandanbao.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.fandanbao.length !== 0">
            <el-col :span="6" v-for="img in item.sign.fandanbao" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">反欺诈风险提示单页：<span v-if="item.sign.fanqizha.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.fanqizha.length !== 0">
            <el-col :span="6" v-for="img in item.sign.fanqizha" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">车辆交接单：<span v-if="item.sign.cheliangjiaojie.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.cheliangjiaojie.length !== 0">
            <el-col :span="6" v-for="img in item.sign.cheliangjiaojie" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">付款时间表：<span v-if="item.sign.fukuanshijian.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.fukuanshijian.length !== 0">
            <el-col :span="6" v-for="img in item.sign.fukuanshijian" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">汽车租赁合同：<span v-if="item.sign.qichezuling.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.qichezuling.length !== 0">
            <el-col :span="6" v-for="img in item.sign.qichezuling" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">授权委托书：<span v-if="item.sign.shouquan.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.shouquan.length !== 0">
            <el-col :span="6" v-for="img in item.sign.shouquan" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">租赁申请核准通知函：<span v-if="item.sign.zulinghezhun.length === 0">暂无图片</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.zulinghezhun.length !== 0">
            <el-col :span="6" v-for="img in item.sign.zulinghezhun" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
          <el-row>
            <el-col :span="12">其他资料：<span v-if="item.sign.other.length === 0">暂无</span></el-col>
          </el-row>
          <el-row :gutter="20" v-if="item.sign.other.length !== 0">
            <el-col :span="6" v-for="img in item.sign.other" :key="img"><a :href="img" target="_blank"><img class="detail-image" :src="img" alt=""></a></el-col>
          </el-row>
        </div>
      </el-tab-pane>
      <el-tab-pane v-if="stage > 4" label="财务审核意见" name="finance">
        <el-row>
          <el-col :span="12">审核状态：{{ item.finance.status === 1?'通过':'驳回' }}</el-col>
        </el-row>
        <el-row>
          <el-col :span="12">备注：{{ item.finance.opinion }}</el-col>
        </el-row>
        <el-row>
          <el-col :span="12">收款人(借款人)姓名：{{ item.finance.customer_name }}</el-col>
        </el-row>
        <el-row>
          <el-col :span="12">银行名：{{ item.finance.bank_name }}</el-col>
          <el-col :span="12">收款人银行卡账户：{{ item.finance.bank_number }}</el-col>
        </el-row>
        <el-row>
          <el-col :span="12">币别：{{ item.finance.money_type }}</el-col>
          <el-col :span="12">收款(借款)金额小写 (大写)：{{ item.finance.money }}</el-col>
        </el-row>
        <el-row>
          <el-col :span="12">支付方式：{{ item.finance.lean_type }}</el-col>
          <el-col :span="12">日期：{{ item.finance.date }}</el-col>
        </el-row>
        <!-- <el-row>
          <el-col :span="12">备注：</el-col>
        </el-row> -->
        <el-row>
          <table class="attachment_table" border="1" style="border-spacing: 0;">
              <tr>
                <td>业务员</td>
                <td>手续费</td>
                <td>银行信息</td>
              </tr>
              <tr>
                <td>{{ item.finance.attachment?item.finance.attachment[0].fee_descript:'暂无' }}</td>
                <td>{{ item.finance.attachment?item.finance.attachment[0].fee:'暂无' }}</td>
                <td>{{ item.finance.attachment?item.finance.attachment[0].bank_info:'暂无' }}</td>
              </tr>
              <tr>
                <td>业务员</td>
                <td>返点金额</td>
                <td>银行信息</td>
              </tr>
              <tr>
                <td>{{ item.finance.attachment?item.finance.attachment[1].fee_descript:'暂无' }}</td>
                <td>{{ item.finance.attachment?item.finance.attachment[1].fee:'暂无' }}</td>
                <td>{{ item.finance.attachment?item.finance.attachment[1].bank_info:'暂无' }}</td>
              </tr>
          </table>
        </el-row>
        <el-button style="margin-top: 20px;" type="primary" @click="onDownload">文件下载</el-button>
      </el-tab-pane>
    </el-tabs> 
  </el-dialog>
</template>
<script>
export default {
  props:['item','stage'],
  data(){
    return {
      visible: true,
      activeName: 'bill_info' 
    }
  },
  filters:{
    billStatusFilter(val){
      switch(val){
        case -1: return '审核驳回';
        case 0: return '草稿';
        case 1: return '风控专员审核中';
        case 2: return '风控总监审核中';
        case 3: return '客服专员签约中';
        case 4: return '财务审批中';
        case 5: return '审核完成';
      }
    },
    levelFilter(val){
      if(val >= 350 && val <= 500){
        return 'D级';
      }else if(val >= 501 && val <= 600){
        return 'C级';
      }else if(val >= 601 && val <= 700){
        return 'B级';
      }else if(val >= 701 && val <= 800){
        return 'A级';
      }else if(val >= 801 && val <= 1000){
        return 'AA级';
      }else{
        return '无';
      }
    },
    interestsFilter(val){
      let interests = [
        {name: '1.4%', value: 0.014},
        {name: '1.6%', value: 0.016},
        {name: '2.5%', value: 0.025},
        {name: '3%', value: 0.03},
        {name: '4%', value: 0.04}
      ]
      let target = interests.find(item => item.value === val);
      if(target){
        return target.name;
      }else{
        return val;
      }
    },
    cardDict(val, key){
      let card = {
        sex:[
          {name: '男', value: 9},
          {name: '女', value: 30}
        ],
        age:[
          {name: '18-25岁', value: 21},
          {name: '26-35岁', value: 42},
          {name: '36-45岁', value: 70},
          {name: '46-55岁', value: 28},
          {name: '56-60岁', value: 7}
        ],
        household:[
          {name: '本市户籍', value: 100},
          {name: '本省内非本市户籍', value: 60},
          {name: '省外户籍（非港澳台地区）', value: 40},
          {name: '港澳台地区', value: 10}
        ],
        marrie_status:[
          {name: '未婚或其他', value: 60},
          {name: '已婚', value: 100},
          {name: '离异', value: 10}
        ],
        education:[
          {name: '本科及以上', value: 40},
          {name: '大专', value: 24},
          {name: '高中', value: 12},
          {name: '初中及以下', value: 4}
        ],
        living_status:[
          {name: '本人或直系亲属名下商品房', value: 200},
          {name: '本人或直系亲属名下小产权房', value: 160},
          {name: '租赁房（商品房或小产权房）', value: 100},
          {name: '租赁房（农民房）', value: 60},
          {name: '公司宿舍', value: 20}
        ],
        family:[
          {name: '与配偶、子女或父母同住', value: 100},
          {name: '与兄弟姐妹同住', value: 60},
          {name: '其他', value: 10}
        ],
        work:[
          {name: '自雇人士', value: 40},
          {name: '授薪人士（公司经理级以上管理人员）', value: 24},
          {name: '授薪人士（公司普通员工）', value: 12},
          {name: '无业人员', value: 4}
        ],
        income:[
          {name: '10000元（含）以内', value: 12},
          {name: '10000元-20000元（含）', value: 24},
          {name: '20000元-30000元（含）', value: 36},
          {name: '30000元-40000元（含）', value: 60},
          {name: '40000元-50000元（含）', value: 84},
          {name: '50000元以上', value: 120}
        ],
        loan_type:[
          {name: '全款车押证', value: 48},
          {name: '全款车押车', value: 80},
          {name: '按揭车不押车', value: 8},
          {name: '按揭车押车', value: 24}
        ],
        car_policy:[
          {name: '全险（至少包括第三者责任、盗抢、车损及不计免赔）', value: 60},
          {name: '第三者责任、盗抢或车损、不计免赔', value: 24},
          {name: '第三者责任、不计免赔', value: 6}
        ],
        face_review:[
          {name: '非常好', value: 60},
          {name: '良好', value: 42},
          {name: '一般', value: 30},
          {name: '较差', value: 6}
        ]
      }
      let target = card[key].find(item => item.value === val);
      if(target){
        return target.name;
      }else{
        return '查询不到';
      }
    }
  },
  computed:{
    total_score(){
      let total = 0;
      for(let key in this.item.wind_control.score_card){
        if(key !== 'score' && key !== 'total_score'){
          total += this.item.wind_control.score_card[key];
        }
      }
      return total;
    }
  },
  methods:{
    closeEvent(){
      const that = this;
      this.visible = false;
      setTimeout(()=>{that.$emit('close');},500);
    },
    onDownload(){
      let uid = sessionStorage.getItem('ts_user_id');
      window.open(`/v1/inspect/${uid}/finance/file?finance_id=${this.item.finance.finance_id}`);
    }
  }
}
</script>
<style>
  .m-orderManage-detailDialog .detail-title {
    line-height: 24px;
    font-size: 16px;
    color: #2d2f33;
    border-bottom: 1px #ddd solid;
    margin-bottom: 10px;
  }
  .m-orderManage-detailDialog .detail-image{
    width:100%;
    padding:5px;
    border:1px #ddd solid;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 200px;
    margin: 0;
  }
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
  .m-orderManage-detailDialog .scord_card_table td{
    min-width: 120px;
    padding: 0 10px;
  }
  .m-orderManage-detailDialog .attachment_table td{
    min-width: 200px;
    padding: 0 5px;
  }
</style>




